  position: absolute;
  top: 30%;
  left: 40%;

  border: 1px solid gainsboro;
  border-radius: 3px;

  padding: 10px;
  width: 400px;
}

dialog input {
  padding: 3px;
}

dialog p {
  color: red;
  font-size: 0.8em;
}

