  width: 60%;
  padding: 2% 0;
  justify-self: center;

  display: flex;
  flex-direction: column;
  gap: 20px;
}

#main-title {
  display: flex;
  justify-content: space-between;

}

#main-title button {
  border: 0;
  background-color: white;
  padding: 8px;
  font-weight: bold;
  font-size: 0.5em;
}

#main-title .add-task:hover {
  background-color: rgba(0,0,0,0.2);
  border-radius: 8px;

  cursor: pointer;
}

#main-title .add-task:active {
  background-color: rgba(0,0,0,0.4);
}

#main-body {
  flex-grow: 1;

  display: flex;
  flex-direction: column;
  gap: 10px;
}

#main-body .task {
  display: flex;
  justify-content: space-between;
}`, "",{"version":3,"sources":["webpack://./src/styles/mainContent.css"],"names":[],"mappings":"AAAA;EACE,UAAU;EACV,aAAa;EACb,oBAAoB;;EAEpB,aAAa;EACb,sBAAsB;EACtB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,8BAA8B;;AAEhC;;AAEA;EACE,SAAS;EACT,uBAAuB;EACvB,YAAY;EACZ,iBAAiB;EACjB,gBAAgB;AAClB;;AAEA;EACE,iCAAiC;EACjC,kBAAkB;;EAElB,eAAe;AACjB;;AAEA;EACE,iCAAiC;AACnC;;AAEA;EACE,YAAY;;EAEZ,aAAa;EACb,sBAAsB;EACtB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,8BAA8B;AAChC","sourcesContent":["#main-content {\n  width: 60%;\n  padding: 2% 0;\n  justify-self: center;\n\n  display: flex;\n  flex-direction: column;\n  gap: 20px;\n}\n\n#main-title {\n  display: flex;\n  justify-content: space-between;\n\n}\n\n#main-title button {\n  border: 0;\n  background-color: white;\n  padding: 8px;\n  font-weight: bold;\n  font-size: 0.5em;\n}\n\n#main-title .add-task:hover {\n  background-color: rgba(0,0,0,0.2);\n  border-radius: 8px;\n\n  cursor: pointer;\n}\n\n#main-title .add-task:active {\n  background-color: rgba(0,0,0,0.4);\n}\n\n#main-body {\n  flex-grow: 1;\n\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n}\n\n#main-body .task {\n  display: flex;\n  justify-content: space-between;\n}"],"sourceRoot":""}]);
