  margin: 0;
  padding: 0;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Verdana, sans-serif;
}

#main {
  display: grid;
  grid-template-rows: auto 1fr;
  grid-template-columns: minmax(300px, 20vw) 1fr;

  height: 100vh;
}

#banner {
  background-color: rgb(58, 56, 56);
  color: white;
  height: 10vh;

  grid-row: 1 / 2;
  grid-column: 1 / 3;

  display: flex;
  align-items: center;
}

#title {
  margin-left: 20px;
}

#nav {
  background-color: rgb(218, 211, 211);
  
  grid-row: 2 / 3;
  grid-column: 1 / 2;
}

#main-content {
  grid-row: 2 / 3;
  grid-column: 2 / 3;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAAA;EACE,SAAS;EACT,UAAU;EACV,6GAA6G;AAC/G;;AAEA;EACE,aAAa;EACb,4BAA4B;EAC5B,8CAA8C;;EAE9C,aAAa;AACf;;AAEA;EACE,iCAAiC;EACjC,YAAY;EACZ,YAAY;;EAEZ,eAAe;EACf,kBAAkB;;EAElB,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,oCAAoC;;EAEpC,eAAe;EACf,kBAAkB;AACpB;;AAEA;EACE,eAAe;EACf,kBAAkB;AACpB","sourcesContent":["* {\n  margin: 0;\n  padding: 0;\n  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Verdana, sans-serif;\n}\n\n#main {\n  display: grid;\n  grid-template-rows: auto 1fr;\n  grid-template-columns: minmax(300px, 20vw) 1fr;\n\n  height: 100vh;\n}\n\n#banner {\n  background-color: rgb(58, 56, 56);\n  color: white;\n  height: 10vh;\n\n  grid-row: 1 / 2;\n  grid-column: 1 / 3;\n\n  display: flex;\n  align-items: center;\n}\n\n#title {\n  margin-left: 20px;\n}\n\n#nav {\n  background-color: rgb(218, 211, 211);\n  \n  grid-row: 2 / 3;\n  grid-column: 1 / 2;\n}\n\n#main-content {\n  grid-row: 2 / 3;\n  grid-column: 2 / 3;\n}"],"sourceRoot":""}]);
