___CSS_LOADER_EXPORT___.push([module.id, `#nav {
  display: flex;
  flex-direction: column;
  gap: 1%;
}

#default-list {
  flex-basis: 12%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

#project {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap:2%;
}

#project #proj-form {
  display: none;
  grid-template-columns: 1fr;
  gap: 20%;
}

#name-input input {
  display: block;
  border: none;
  border-radius: 3px;
  padding: 5px;
  font-size: 1.1em;
}

.action-btns {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 5%;
}

.action-btns button {
  font-size: 1.2em;
  padding: 5px 15px;
  border: none;
  border-radius: 5px;
  text-align: center;
  flex-grow: 1;
}

.action-btns button:hover {
  filter: saturate(0.6);
  cursor: pointer;
}

.action-btns button:active {
  filter: saturate(0.1);
}

.action-btns .confirm {
  background-color: aquamarine;
}

.action-btns .cancel {
  background-color: rgb(239, 120, 143);
}

#project-title {
  font-size: 1.6em;
}

#add-proj {
  font-size: 1.15em;
}

#project-list {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 1%;
}

.navigation {
  padding: 4% 10% 0;
}

.task-nav {
  padding: 10px 10px;
  background: none;
  border: none;
  text-align: left;

  font-size: 1em;
  width: 100%;
}

.task-nav:hover {
  background-color: rgba(0,0,0,0.2);
  border-radius: 8px;

  cursor: pointer;
}

.task-nav:active {
  background-color: rgba(0,0,0,0.4);
}

.active {
  background-color: rgba(0,0,0,0.2);
  border-radius: 8px;
}`, "",{"version":3,"sources":["webpack://./src/styles/navBar.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,sBAAsB;EACtB,OAAO;AACT;;AAEA;EACE,eAAe;EACf,aAAa;EACb,sBAAsB;EACtB,6BAA6B;AAC/B;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,sBAAsB;EACtB,MAAM;AACR;;AAEA;EACE,aAAa;EACb,0BAA0B;EAC1B,QAAQ;AACV;;AAEA;EACE,cAAc;EACd,YAAY;EACZ,kBAAkB;EAClB,YAAY;EACZ,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,OAAO;AACT;;AAEA;EACE,gBAAgB;EAChB,iBAAiB;EACjB,YAAY;EACZ,kBAAkB;EAClB,kBAAkB;EAClB,YAAY;AACd;;AAEA;EACE,qBAAqB;EACrB,eAAe;AACjB;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,4BAA4B;AAC9B;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,sBAAsB;EACtB,OAAO;AACT;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,kBAAkB;EAClB,gBAAgB;EAChB,YAAY;EACZ,gBAAgB;;EAEhB,cAAc;EACd,WAAW;AACb;;AAEA;EACE,iCAAiC;EACjC,kBAAkB;;EAElB,eAAe;AACjB;;AAEA;EACE,iCAAiC;AACnC;;AAEA;EACE,iCAAiC;EACjC,kBAAkB;AACpB","sourcesContent":["#nav {\n  display: flex;\n  flex-direction: column;\n  gap: 1%;\n}\n\n#default-list {\n  flex-basis: 12%;\n  display: flex;\n  flex-direction: column;\n  justify-content: space-evenly;\n}\n\n#project {\n  flex-grow: 1;\n  display: flex;\n  flex-direction: column;\n  gap:2%;\n}\n\n#project #proj-form {\n  display: none;\n  grid-template-columns: 1fr;\n  gap: 20%;\n}\n\n#name-input input {\n  display: block;\n  border: none;\n  border-radius: 3px;\n  padding: 5px;\n  font-size: 1.1em;\n}\n\n.action-btns {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  gap: 5%;\n}\n\n.action-btns button {\n  font-size: 1.2em;\n  padding: 5px 15px;\n  border: none;\n  border-radius: 5px;\n  text-align: center;\n  flex-grow: 1;\n}\n\n.action-btns button:hover {\n  filter: saturate(0.6);\n  cursor: pointer;\n}\n\n.action-btns button:active {\n  filter: saturate(0.1);\n}\n\n.action-btns .confirm {\n  background-color: aquamarine;\n}\n\n.action-btns .cancel {\n  background-color: rgb(239, 120, 143);\n}\n\n#project-title {\n  font-size: 